@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat+Subrayada:wght@700&display=swap);
*{margin:0;padding:0}html{scroll-behavior:smooth}body{font-size:calc(12px + (20 - 14) * ((100vw - 300px) / (1600 - 300)));font-family:"Poppins",sans-serif}body{overflow-x:hidden;background:linear-gradient(-90deg, rgba(0, 0, 0, 0.05) 1px, transparent 1px),linear-gradient(rgba(0, 0, 0, 0.05) 1px, transparent 1px),linear-gradient(-90deg, rgba(0, 0, 0, 0.04) 1px, transparent 1px),linear-gradient(rgba(0, 0, 0, 0.04) 1px, transparent 1px),linear-gradient(transparent 3px, #c7c0b8 3px, #c7c0b8 78px, transparent 78px),linear-gradient(-90deg, #aaa 1px, transparent 1px),linear-gradient(-90deg, transparent 3px, #c7c0b8 3px, #c7c0b8 78px, transparent 78px),linear-gradient(#aaa 1px, transparent 1px),#c7c0b8;background-size:4px 4px,4px 4px,80px 80px,80px 80px,80px 80px,80px 80px,80px 80px,80px 80px}::selection{background:#fff}::-moz-selection{background:#fff}::-webkit-scrollbar{width:5px}::-webkit-scrollbar-track{background:rgba(0,0,0,.5)}::-webkit-scrollbar-thumb{border-radius:0px;background:#000}
.App{position:relative}.App a{color:#000;text-decoration:underline}.App a:hover{color:#fff}@media only screen and (min-width: 100px){.App{padding:20px 20px}}@media only screen and (min-width: 1000px){.App{padding:20px 200px}}
.cursor{width:40px;height:40px;border:2px solid #fefefe;border-radius:100%;position:fixed;transform:translate(-50%, -50%);pointer-events:none;transition:all 150ms ease;transition-property:background-color,opacity,transform,mix-blend-mode;z-index:9999;mix-blend-mode:difference}.cursor--hidden{opacity:0}.cursor--link-hovered{transform:translate(-50%, -50%) scale(1.25);background-color:#fefefe}.cursor--clicked{transform:translate(-50%, -50%) scale(0.9);background-color:#fefefe}html *,body *{cursor:none}
nav{display:flex;justify-content:center;grid-gap:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));gap:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)))}nav a{text-decoration:none !important}
footer{position:relative;min-height:100vh}footer .footer__bg{background:#2a2a2a no-repeat center center fixed;background-size:cover;position:absolute;top:0;bottom:-20px;left:-20px;right:-20px;z-index:-1}footer .contact__description{display:flex;flex-direction:column;align-items:flex-end}footer .contact__description p{color:#fff;text-align:right;font-size:1.5em;font-weight:600;max-width:250px;padding:50px 0}footer .contact__description a{color:#fff}footer .copyright{color:#fff;display:flex;position:absolute;bottom:0px;left:0;right:0;justify-content:center}@media only screen and (min-width: 100px){footer .contact__description p{max-width:300px}}@media only screen and (min-width: 1000px){footer .footer__bg{left:-200px;right:-200px}footer .contact__description p{max-width:500px}}
.about{display:flex;flex-direction:column;height:calc(100vh - 20px)}.about p{margin-top:10px}.about .about__intro{text-align:right;flex-direction:column;margin-top:150px}.about .about__intro .about__intro__title{font-family:"Montserrat Subrayada",sans-serif;font-size:calc(26px + (72 - 26) * ((100vw - 300px) / (1600 - 300)))}.about .about__intro .about__intro__subtitle{font-weight:600;font-size:1.5em}.about .about__description{margin-top:50px;transition:.5s max-height cubic-bezier(0.19, 1, 0.22, 1);overflow:hidden}.about .about__description__show{max-height:300px}.about .about__description__hide{max-height:0px}.about .about__more{text-align:right;position:relative;margin-top:25px}.about .about__more a{text-decoration:none}
#work h1{font-family:"Press Start 2P",cursive;font-size:5em;margin-bottom:50px}#work .work__item{min-height:300px;display:grid;grid-template-columns:2fr 1fr 1fr}#work .work__item h2{font-size:3em}#work .work__item .work__image{margin:10px;background-color:#000}#work .work__item .work__date{display:flex;justify-content:flex-end}
